<template>
  <el-dialog title="请输入拒收原因？" :visible.sync="rejectionShow" :close-on-click-modal="false" :close-on-press-escape="false" width="30%">
    <el-input v-model.trim="rejectionForm.refundReason" type="textarea" placeholder="请输入拒收原因" maxlength="50" show-word-limit></el-input>
    <div class="flex-row-end mt20">
      <el-button @click="rejectionShowClose">取 消</el-button>
      <el-button :loading="rejectionLoading" type="primary" @click="rejectionFun">确 定</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  props: {
    rejectionShow: {
      type: Boolean,
      default: false
    },
    rejectionForm:{
      type:Object,
    },
    rejectionLoading:{
      type: Boolean,
      default: false
    }
  },
  methods:{
    rejectionFun(){
      if(!this.rejectionForm.refundReason){
        this.$message.error('请输入拒收原因');
        return;
      }
      this.$emit('rejectionFun', this.rejectionForm);
    },
    rejectionShowClose(){
      this.$emit('rejectionShowClose', true);
    }
  }
}
</script>
