<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <el-tabs v-model="searchList.tabType" @tab-click="getData">
        <el-tab-pane label="全部" name="0"></el-tab-pane>
        <el-tab-pane label="待审核" name="10"></el-tab-pane>
        <el-tab-pane label="待交货" name="24"></el-tab-pane>
        <el-tab-pane label="已交货" name="30"></el-tab-pane>
        <el-tab-pane label="已收货" name="32"></el-tab-pane>
        <el-tab-pane label="退款中" name="40"></el-tab-pane>
        <el-tab-pane label="已退款" name="50"></el-tab-pane>
        <el-tab-pane label="已驳回" name="20"></el-tab-pane>
        <el-tab-pane label="退款失败" name="70"></el-tab-pane>
        <el-tab-pane label="已拒收" name="80"></el-tab-pane>
        <el-tab-pane label="已撤销" name="60"></el-tab-pane>
      </el-tabs>
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="订单编号：">
            <el-input style="width: 203px" v-model="searchList.orderNo" clearable placeholder="订单编号"></el-input>
          </el-form-item>
          <el-form-item label="退单编号：">
            <el-input style="width: 203px" v-model="searchList.afterSaleNo" clearable placeholder="退单编号"></el-input>
          </el-form-item>
          <el-form-item label="申请时间：">
            <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="商品搜索：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="商品名称/商品编码"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table v-loading="formLoading" :data="list" border tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="afterSaleNo" label="退货单号" width="260"></el-table-column>
          <el-table-column label="订单编号" width="260">
            <template slot-scope="scope">
              <div class="brandFont curP" @click="$router.push({path:'/order/orderList/orderinfo',query:{orderId:scope.row.orderId}})">{{scope.row.orderNo}}</div>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="申请时间" width="300"></el-table-column>
          <el-table-column prop="name" label="商品信息" width="400"></el-table-column>
          <el-table-column prop="productCount" label="申请退货量" width="180"></el-table-column>
          <el-table-column prop="applyAmount" label="申请退款金额" width="180"></el-table-column>
          <el-table-column label="退款原因" width="300">
            <template slot-scope="scope">
              {{scope.row.questionDesc ? scope.row.questionDesc : '-'}}
            </template>
          </el-table-column>
          <el-table-column label="退款状态" width="180">
            <template slot-scope="scope">
              <div style="color:#E6A23C;" v-if="scope.row.auditStatus == 10">待审核</div>
              <div style="color:#E6A23C;" v-if="scope.row.auditStatus == 24">待交货</div>
              <div style="color:#67C23A;" v-if="scope.row.auditStatus == 30">已交货</div>
              <div style="color:#67C23A;" v-if="scope.row.auditStatus == 32">已收货</div>
              <div style="color:#E6A23C;" v-if="scope.row.auditStatus == 40">退款中</div>
              <div style="color:#67C23A;" v-if="scope.row.auditStatus == 50">已退款</div>
              <div style="color:#F56C6C;" v-if="scope.row.auditStatus == 20">已驳回</div>
              <div style="color:#F56C6C;" v-if="scope.row.auditStatus == 60">已撤销</div>
              <div style="color:#F56C6C;" v-if="scope.row.auditStatus == 70">退款失败</div>
              <div style="color:#F56C6C;" v-if="scope.row.auditStatus == 80">已拒收</div>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="180" fixed="right">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="$router.push({path:'/afterSaleManage/returnRefund/returnRefundInfo',query:{id:scope.row.id}})">查看</el-button>
              <el-button @click="agreeItem(scope.row)" v-if="scope.row.auditStatus == 10" style="color:#E6A23C;" type="text" size="small">同意退货</el-button>
              <el-button @click="rejectItem(scope.row)" v-if="scope.row.auditStatus == 10" style="color:#F56C6C;" type="text" size="small">驳回</el-button>
              <el-button @click="getItem(scope.row)" v-if="scope.row.auditStatus == 30" style="color:#E6A23C;" type="text" size="small">确认收货</el-button>
              <el-button @click="noItem(scope.row)" v-if="scope.row.auditStatus == 30" style="color:#F56C6C;" type="text" size="small">拒绝退款</el-button>
              <el-button @click="sureItem(scope.row)" v-if="scope.row.auditStatus == 32" style="color:#E6A23C;" type="text" size="small">退款</el-button>
              <el-button @click="againItem(scope.row)" v-if="scope.row.auditStatus == 70" style="color:#E6A23C;" type="text" size="small">再次退款</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[5,10,15,20]"
              :page-size="searchList.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listTotal">
            </el-pagination>
          </div>
        </div>
      </el-card>
    </div>
    <!-- 驳回弹出框 -->
    <rejectShow :rejectShow="rejectShow" :rejectForm="rejectForm" @rejectShowClose="rejectShowClose" @rejectFun="rejectFun"></rejectShow>
    <!-- 同意退货弹出框 -->
    <showAddress :addressShow="addressShow" @addressShowClose="addressShowClose" @addressFun="addressFun"></showAddress>
    <!-- 拒收弹出框 -->
    <rejectionShow :rejectionShow="rejectionShow" :rejectionForm="rejectionForm" :rejectionLoading="rejectionLoading" @rejectionShowClose="rejectionShowClose" @rejectionFun="rejectionFun"></rejectionShow>
    <!-- 退款弹出框 -->
    <makeSureShow :makeSureShow="makeSureShow" :makeSureId="makeSureId" :makeSureLoading="makeSureLoading" @makeSureClose="makeSureClose" @makeSureFun="makeSureFun"></makeSureShow>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { continueRefund} from '@/api/afterSaleManage/refund'
import { findByCondition , reject , approve , rejectReason ,approveAmount } from '@/api/afterSaleManage/returnRefund'
import rejectShow from '../components/rejectShow';
import rejectionShow from '../components/rejectionShow';
import showAddress from '../components/showAddress';
import makeSureShow from '../components/makeSureShow';
export default {
  data(){
    return{
      formLoading:false,
      searchList:{
        tabType:0,
        pageNum:1,
        pageSize:5,
        time:[]
      },
      list:[],
      listTotal:0,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      // 驳回
      rejectShow:false,
      rejectForm:{},
      // 同意退货
      addressShow:false,
      addressLoading:true,
      agreeForm:{},
      // 拒收
      rejectionShow:false,
      rejectionForm:{},
      rejectionLoading:false,
      // 确认收货
      makeSureId:"",
      makeSureShow:false,
      makeSureLoading:false
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    if(this.$route.query.tabType) this.searchList.tabType = this.$route.query.tabType+''
    this.getData();
  },
  methods:{
    // 列表 - 获取数据
    getData(){
      this.formLoading = true;
      this.searchList.startCreateTime = this.searchList.time[0] || ''
      this.searchList.endCreateTime = this.searchList.time[1] || ''
      this.searchList.auditStatus = this.searchList.tabType == 0 ? '' : this.searchList.tabType
      console.log('请求的数据',this.searchList)
      findByCondition(this.searchList).then(res=>{
        console.log('数据列表',res)
        if(res.code == 200){
          this.list = res.data.list;
          this.listTotal = res.data.total
        }
        this.formLoading = false;
      })
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.name = ''
      this.searchList.orderNo = ''
      this.searchList.afterSaleNo = ''
      this.searchList.time = []
      this.getData()
    },
    makeSureFun(e){
      console.log('父',e)
      this.makeSureLoading = true;
      approveAmount(e).then(res=>{
        console.log('同意退款',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.makeSureShow = false;
          this.getData()
          setTimeout(()=>{
            this.makeSureLoading = false;
          },1000)
        }else{
          this.$message.error(res.message);
          this.makeSureLoading = false;
        }
      })
    },
    makeSureClose(){
      this.makeSureShow = false;
    },
    // 
    sureItem(e){
      this.makeSureId = e.id
      this.makeSureShow = true;
    },
    // 已交货 - 拒绝退款
    noItem(e){
      this.rejectionShow = true;
      this.rejectionForm.id = e.id;
      this.rejectionForm.state = 80;
    },
    rejectionShowClose(e){
      this.rejectionShow = false;
    },
    rejectionFun(e){
      console.log('父',e)
      this.rejectionLoading = true;
      rejectReason(e).then(res=>{
        console.log('拒收',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.rejectionShow = false
          this.getData()
          setTimeout(()=>{
            this.rejectionLoading = false;
          },1000)
        }else{
          this.rejectionLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 列表 - 驳回
    rejectItem(e){
      this.rejectForm.id = e.id
      this.rejectShow = true
    },
    // 驳回 - 关闭
    rejectShowClose(e){
      console.log('父级关闭',e)
      this.rejectShow = false
    },
    // 驳回 - 请求
    rejectFun(e){
      console.log('父级确认',e)
      this.rejectLoading = true;
      reject(e).then(res => {
        console.log('驳回',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.rejectForm.reason = ''
          this.getData()
          this.rejectShow = false;
          setTimeout(()=>{
            this.rejectLoading = false;
          },1000)
        }else{
          this.rejectLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 列表 - 重新退款
    againItem(e){
      this.$confirm(`退款金额：￥${e.refundAmount}`, '再次退款确认', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        continueRefund({id:e.id}).then(res=>{
          console.log('重新退款',res)
          if(res.code == 200){
            this.$message({
              message: res.message,
              type: 'success'
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 已交货 - 确认收货
    getItem(e){
      this.$confirm('您确认已经收到卖家的退货吗?', '收货确认', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let obj = {
          id:e.id,
          state:32
        }
        approve(obj).then(res=>{
          console.log('确认收货',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: res.message
            });
            this.getData()
          }else{
            this.$message.error(res.message);
          }
        })
        
      }).catch(() => {});
    },
    // 同意退货退款
    agreeItem(e){
      console.log(e)
      this.addressShow = true;
      this.agreeForm.id = e.id
      this.agreeForm.state = 24
    },
    addressShowClose(){
      this.addressShow = false;
      this.agreeForm.id = ''
      this.agreeForm.state = ''
    },
    addressFun(){
      console.log('执行了')
      this.addressLoading = true;
      approve(this.agreeForm).then(res=>{
        console.log('同意退货退款',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.addressShow = false;
          this.getData()
          setTimeout(()=>{
            this.addressLoading = false;
          },1000)
        }else{
          this.addressLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    }
  },
  components:{
    rejectShow,
    showAddress,
    rejectionShow,
    makeSureShow
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
  .active {
    height: 70px;
  }
</style>