<template>
  <el-dialog title="您要通过该退货申请吗？" :visible.sync="addressShow" width="30%">
    <span>同意后，买家将会看到您的退货地址。</span>
    <div>
      <el-button @click="newPage" type="text">还没维护？去维护> </el-button>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="addressShowClose">取 消</el-button>
      <el-button :loading="addressLoading" type="primary" @click="addressShowTrue">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
export default {
  props: {
    addressShow: {
      type: Boolean,
      default: false
    },
    addressLoading:{
      type: Boolean,
      default: false
    }
  },
  methods:{
    addressShowClose(){
      this.$emit('addressShowClose', true);
    },
    addressShowTrue(){
      this.$emit('addressFun', true);
    },
    // 打开运费模板新页面
    newPage(){
      let details = this.$router.resolve({
        name: "物流地址",
      });
      window.open(details.href, "_blank")
    },
  }
}
</script>